body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #eef1f3;
}

.modal-label {
    display: block;
    text-align: left;
}

.modal-form {
    width: 300px;
    text-align: center;
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
    z-index: 9999;
}

.input-group.four-input {
    width: 22%;
    border-right-width: 1px;
    display: -webkit-box;
    padding-top: 25px;
}

.four-input-label {
    margin-top: 5px;
    margin-right: 10px;
}

.enter-table-values {
    width: 94%;
    display: inline-block;
    padding-top: 30px;
}

.create-table {
    text-align: -webkit-center;
    width: 80%;
    display: inline-block;
    padding-top: 30px;
}

.enter-fields-info {
  display: inline-block;
  margin: auto;
  padding: 0px 20px 20px 20px;
}

.mega-menu-row.row {
    margin: auto;
    padding-top: 7px;
}

.react-bs-table-tool-bar .col-xs-6 {
    width: 100%;
    padding-bottom: 5px;
}

.react-bs-table-container {
  padding-bottom: 10px;
}



